___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --boardSize: 10;
  --gameBoardSize: 600;
}

html,
body {
  width: 100%;
  min-height: 100vh;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  background-color: midnightblue;
}

h1 {
  font-family: Didot, 'Bodoni MT', 'Noto Serif Display', 'URW Palladio L', P052, Sylfaen, serif;
  font-weight: normal;
  color: white;
  font-size: 4rem;
}

.boards {
  display: flex;
  gap: 100px;
}

.grouping {
  height: calc((var(--gameBoardSize) + 100) * 1px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.buttons {
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 450px;
}

button {
  width: 250px;
  height: 3rem;
  outline: none;
  border: none;
  font-size: 1.4rem;
  cursor: pointer;
  font-family: Didot, 'Bodoni MT', 'Noto Serif Display', 'URW Palladio L', P052, Sylfaen, serif;
  border-radius: 20px;
}

#randomize {
  background-color: white;
}

#randomize:hover {
  background-color: rgb(211, 210, 210);
}

#start {
  background-color: rgb(0, 150, 0);
  color: white;
}

#start:hover {
  background-color: rgb(0, 100, 0);
}

h2 {
  font-family: Optima, Candara, 'Noto Sans', source-sans-pro, sans-serif;
  font-weight: normal;
  color: white;
  font-size: 2rem;
}

.gameboard {
  display: grid;
  grid-template-columns: repeat(var(--boardSize), 1fr);
  grid-template-rows: repeat(var(--boardSize), 1fr);
  width: calc(var(--gameBoardSize) * 1px);
  height: calc(var(--gameBoardSize) * 1px);
  background-color: lightskyblue;
  box-shadow: 5px 5px 7px rgba(0, 0, 0, 0.5);
}

.tile {
  border: 1px dashed black;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: calc((var(--gameBoardSize) / 15) * 1px);
  user-select: none;
}

.user {
  cursor: default;
}

.computer .tile {
  cursor: pointer;
}

.computer .tile:hover {
  background-color: rgb(94, 172, 221);
}
